import React, { useEffect, useRef, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useSearchUniversitiesQuery } from '../../services/university';
import {
  selectItem,
  unselectItem,
  unselectAll,
} from '../../store/selectedItemsSlice';
import noData from '/public/no-data.png';
import { RootState } from '@/store/store';
import './/ResultsContainer.css';
import { setUniversities } from '@/store/searchSlice';

const ITEM_PER_PAGE = 10;

const ResultsContainer = (): JSX.Element => {
  const dispatch = useDispatch();
  const { searchTerm, currentPage } = useSelector(
    (state: RootState) => state.search
  );

  const {
    data: universities = [],
    refetch,
    isLoading,
  } = useSearchUniversitiesQuery({
    country: searchTerm,
    searchKey: currentPage,
  });

  const downloadLinkRef = useRef<HTMLAnchorElement | null>(null);
  const [customIsLoading, setCustomIsLoading] = useState(false);

  useEffect(() => {
    if (universities) {
      dispatch(setUniversities(universities));
    }
  }, [universities, dispatch]);

  const downloadCSV = (items: University[]) => {
    const csv = items
      .map((item) => `${item.name},${item.country},${item.web_pages[0]}`)
      .join('\n');
    const blob = new Blob([csv], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    if (downloadLinkRef.current) {
      downloadLinkRef.current.href = url;
      downloadLinkRef.current.download = `${items.length}_items.csv`;
      downloadLinkRef.current.click();
      setTimeout(() => URL.revokeObjectURL(url), 100);
    }
  };

  const handleSelectUniversity = (university: University) => {
    dispatch({ type: 'search/setSelectedUniversity', payload: university });
  };

  const handleCloseDetailsPage = () => {
    dispatch({ type: 'search/setCurrentPage', payload: currentPage });
  };

  const handleNextPage = () => {
    dispatch({ type: 'search/setCurrentPage', payload: currentPage + 1 });
  };

  const handlePreviousPage = () => {
    if (currentPage > 1) {
      dispatch({ type: 'search/setCurrentPage', payload: currentPage - 1 });
    }
  };

  const handleRefresh = () => {
    setCustomIsLoading(true);
    setTimeout(() => setCustomIsLoading(false), 1000);
    refetch();
  };

  if (isLoading || customIsLoading) {
    return (
      <div className="loader-container">
        <p className="loader-message">Loading universities, please wait...</p>
      </div>
    );
  }

  const isSelected = selectItem.some(
    (item: University) => item.domains[0] === universities[0].domains[0]
  );

  return (
    <div className="results-container">
      <button className="refresh-page" onClick={handleRefresh}>
        <i className="bi bi-arrow-clockwise"></i>
      </button>
      <div className="university-list">
        {universities.map((university) => (
          <div
            key={university.domains[0]}
            className="university-card"
            style={{ cursor: 'pointer' }}
            onClick={(e) => {
              if ((e.target as HTMLElement).closest('.select-checkbox')) return;
              handleSelectUniversity(university);
            }}
          >
            <input
              type="checkbox"
              className="select-checkbox"
              checked={isSelected}
              onClick={(e) => e.stopPropagation()}
              onChange={(e) => {
                if (e.target.checked) dispatch(selectItem(university));
                else dispatch(unselectItem(university.domains[0]));
              }}
            />
            <h3>{university.name}</h3>
            <p>
              <strong>Country:</strong> {university.country}
            </p>
            <p>
              <strong>Web Site:</strong>{' '}
              <a
                href={university.web_pages[0]}
                target="_blank"
                rel="noreferrer"
              >
                {university.web_pages[0] || 'No Web Site'}
              </a>
            </p>
          </div>
        ))}
      </div>
      <div className="pagination-controls">
        <button onClick={handlePreviousPage} disabled={currentPage === 1}>
          Previous
        </button>
        <button onClick={handleNextPage}>Next</button>
      </div>
      {selectedItems.length > 0 && (
        <div className="flyout">
          <span>{selectedItems.length} items are selected</span>
          <button onClick={() => dispatch(unselectAll())}>Unselect all</button>
          <button onClick={() => downloadCSV(selectedItems)}>Download</button>
          <a ref={downloadLinkRef} style={{ display: 'none' }}>
            Download
          </a>
        </div>
      )}
    </div>
  );
};

export default ResultsContainer;
